.scr-navi {
  .swiper-wrapper {
    .swiper-slide {
      a {
        font-size: 4vw;
      }
    }
  }
}
.my-info {
  position: relative;
  width: 100%;
  height: 14vw;
  padding: 6.8vw 4.8vw;
  color: #fff;
  z-index: 2;
  .name {
    font-size: 5.066vw;
    margin-bottom: 2.666vw;
    i {
      font-weight: 700;
    }
  }
  .name + div {
    overflow: hidden;
    .left {
      float: left;
      font-size: 3.466vw;
      p {
        display: inline;
        i {
          font-weight: 700;
        }
      }
      .txt {
        display: inline-block;
        font-size: 3.466vw;
        i {
          font-weight: 700;
        }
      }
      .hs-line {
        margin-right: 1.5vw;
        padding-right: 2vw;
        border-right: 2px solid rgba(255, 255, 255, 0.2);
        line-height: .9;
      }
      .hs-dot {
        margin-right: -1.2vw;
      }
    }
    a {
      color: #fff;
      font-size: 3.466vw;
    }
  }
  .barcode-open {
    position: absolute;
    right: 12.5vw;
    bottom: 22.5%;
    width: 4.5vw;
    height: 4.5vw;
  }
  .barcode-layer {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    background: #f7f7f7;
    text-align: center;
    h4 {
      margin: 2vw 0;
      color: #cccccc;
      font-size: 3.3vw;
      font-weight: bold;
    }
    .barcode {
      width: 65vw;
      max-height: 16vw;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
    .barcode-close {
      position: absolute;
      width: 6vw;
      height: 6vw;
      top: 50%;
      right: 6vw;
      transform: translateY(-50%);

      img {
        width: 100%;
      }
    }
    .barcode-label {
      color: #000;
      font-size: 3vw;
      margin-top: .5vw;
    }
  }
}
.my-point {
  line-height: 12vw;
  overflow: hidden;
  padding: 0 6.4vw 4.8vw;
  dt {
    font-size: 3.466vw;
    color: #1f1f1f;
    font-weight: 700;
    height: 12vw;
    float: left;
    width: 50%;
  }
  dd {
    font-size: 4vw;
    color: #000;
    font-weight: 700;
    height: 12vw;
    float: right;
    width: 50%;
    text-align: right;
  }
  .small {
    font-size: 3.2vw;
    color: #aaa;
    line-height: 4vw;
    height: 4vw;
    margin: -2.666vw 0 2.666vw;
    font-weight: 300;
  }
}
.my-family {
  background: #f8f8f8;
  padding: 8.266vw 6.4vw 9.2vw;
  dl {
    overflow: hidden;
    dt {
      color: #898989;
      font-size: 3.466vw;
      width: 60%;
      float: left;
      height: 7.733vw;
      line-height: 7.733vw;
    }
    dd {
      font-size: 4vw;
      color: #000;
      width: 40%;
      float: left;
      text-align: right;
      height: 7.733vw;
      line-height: 7.733vw;
    }
    .bold {
      font-size: 3.733vw;
      color: #1f1f1f;
      line-height: 4.8vw;
      height: 4.8vw;
      margin-bottom: 6vw;
    }
    .btn-wow a {
      font-size: 0;
      text-indent: -9999px;
      display: block;
      width: 26.933vw;
      height: 4.8vw;
      margin-bottom: 6vw;
      background: url('/_ui/img/m/icn/btn-wowmykolon.png') no-repeat;
      background-size: 100%;
      float: right;
    }
  }
  .btn-guide {
    display: block;
    background: #fff;
    height: 10.933vw;
    line-height: 10.933vw;
    color: #1f1f1f;
    font-size: 3.466vw;
    text-align: center;
    margin-top: 4.8vw;
  }
}
