.wow-my-kolon {
  min-height: 300px;
  ul {
    width: 85.066vw;
    margin: 12.266vw auto 0;
    li {
      border-top: .266vw #e1e1e1 solid;
      padding-top: 9.333vw;
      &:first-child {
        border-top: 0;
        padding-top: 0;
      }
      .coupon {
        height: 37.866vw;
        overflow: hidden;
        .left {
          float: left;
          width: 66.4vw;
          height: 37.866vw;
          background: #fff;
          position: relative;
          .tit {
            font-size: 4.266vw;
            color: #1f1f1f;
            font-weight: 700;
            margin: 5.466vw 0 1.866vw 5.333vw;
          }
          small {
            font-size: 3.2vw;
            color: #898989;
            margin-left: 5.333vw;
          }
          .btm {
            overflow: hidden;
            position: absolute;
            bottom: 5.066vw;
            left: 5.333vw;
            width: 416px;
            .discount {
              color: #1f1f1f;
              font-size: 6vw;
            }
            span + span {
              position: absolute;
              bottom: 0;
              right: 0;
              font-size: 2.933vw;
              color: #aaa;
            }
          }
        }
        .right {
          float: left;
          background: #f23d4e;
          width: 18.666vw;
          height: 37.866vw;
          position: relative;
          span {
            color: #fff;
            font-size: 4vw;
            line-height: 5.333vw;
            position: absolute;
            top: 50%;
            left: 0;
            right: 0;
            display: block;
            text-align: center;
            -webkit-transform: translateY(-50%);
            -moz-transform: translateY(-50%);
            -o-transform: translateY(-50%);
            -ms-transform: translateY(-50%);
            transform: translateY(-50%);
          }
          a {
            background: url('/_ui/img/m/icn/icn-arrow10.png') no-repeat center;
            background-size: 3.333vw 4.533vw;
            display: block;
            height: 100%;
            font-size: 2.933vw;
            color: #fff;
            line-height: 62.666vw;
            text-align: center;
          }
        }
      }
      dl {
        overflow: hidden;
        color: #898989;
        font-size: 3.2vw;
        line-height: 4.533vw;
        margin-top: 3.866vw;
        padding-bottom: 8.4vw;
        dt {
          float: left;
          clear: both;
        }
        dd {
          float: left;
          margin-left: .666vw;
        }
        i {
          color: #1f1f1f;
        }
      }
    }
  }
}
.h1-top {
  position: relative;
  .pop-close {
    background: url('/_ui/img/m/icn/icn-close.png') no-repeat;
    background-size: 100%;
    top: 5.333vw;
    right: 4.8vw;
    width: 4vw;
    height: 4vw;
    font-size: 0;
    text-indent: -9999px;
    display: block;
    position: absolute;
  }
  h1 {
    font-size: 4.533vw;
    color: #1f1f1f;
    text-align: center;
    font-weight: 700;
    padding: 5.333vw 0 2.266vw;
  }
  small {
    text-align: center;
    font-weight: 700;
    font-size: 3.466vw;
    color: #898989;
    display: block;
  }
}
.family-guide {
  padding-bottom: 17.066vw;
  img {
    width: 100%;
  }
  h3 {
    font-size: 3.733vw;
    color: #1f1f1f;
    font-weight: 700;
    margin-bottom: 3.466vw;
  }
  .box1 {
    border-bottom: .2266vw #f7f7f7 solid;
    padding: 0 4.8vw;
    .mbship {
      width: 80.533vw;
      margin-left: 8.666vw;
    }
  }
  .box2 {
    padding: 8.4vw 4.8vw 0;
    dl {
      overflow: hidden;
      border-top: .8vw #000 solid;
      border-bottom: .8vw #000 solid;
      line-height: 13.866vw;
      font-size: 3.466vw;
      dt {
        width: 30%;
        float: left;
        clear: both;
        height: 13.866vw;
        color: #898989;
        border-top: .266vw #f1f1f1 solid;
        &:first-child {
          border-top: 0;
        }
      }
      dt:first-child + dd {
        border-top: 0;
      }
      dd {
        width: 70%;
        float: left;
        height: 13.866vw;
        color: #1f1f1f;
        border-top: .266vw #f1f1f1 solid;
        a {
          color: #1f1f1f;
          text-decoration: underline;
        }
      }
    }
  }
  .box3 {
    h3 {
      padding: 0 4vw;
    }
    p {
      padding: 0 4vw;
      font-size: 3.733vw;
      line-height: 5.866vw;
    }
    h3 + p {
      color: #898989;
    }
    p + p {
      color: #1f1f1f;
      margin-top: 2vw;
    }
    .margin {
      margin: 5.066vw 0;
    }
    .notice {
      padding: 0 6.666vw;
    }
  }
  .notice {
    padding-left: 2.666vw;
    padding-bottom: 8.933vw;
    li {
      list-style: disc;
      font-size: 3.733vw;
      color: #898989;
      line-height: 5.333vw;
    }
  }
}
@media (min-width: 750px) {
  .scr-navi {
    width: 678px;
    .swiper-wrapper {
      .swiper-slide a {
        height: 100px;
        line-height: 100px;
        font-size: 30px;
      }
